import React, { useState, useEffect, useRef } from 'react';
import { HubConnectionBuilder } from '@microsoft/signalr';

const Chat = () => {
  // const chatHubEndPoint = "https://localhost:5001/hubs/chat";
  const chatHubEndPoint = "https://aws-chat.azurewebsites.net/hubs/chat";
  const [ connection, setConnection] = useState(null);
  const [ name, setName ] = useState('');
  const [ message, setMessage ] = useState('');
  const [ chat, setChat ] = useState([]);
  const latestChat = useRef(null);
  const nameEl = useRef(null);
  const messageEl = useRef(null);

  latestChat.current = chat;

  // componentDidMount
  useEffect(() => {
    const newConnection = new HubConnectionBuilder()
      .withUrl(chatHubEndPoint)
      .withAutomaticReconnect()
      .build();

    setConnection(newConnection);
  }, []);
  // componentDidUpdate (changes to connection)
  useEffect(() => {
    if (connection) {
      connection.start()
        .then(() => {
          console.log('Connected!');

          connection.on('ReceiveMessage', m => {
            const updatedChat = [...latestChat.current];
            updatedChat.push(m);
            setChat(updatedChat);
          });
        })
        .catch(e => console.log('Connection failed: ', e));
    }
  }, [connection]);
  const handleSubmit = async (e) => {
    e.preventDefault();
    const m = {name: name, message: message};
    setMessage('');
    nameEl.current.disabled = true;
    messageEl.current.focus();
    await connection.send('SendMessage', m);
  }

  return (
    <React.Fragment>
      <form onSubmit={handleSubmit} className='Chat-form'>
      <input 
          ref={nameEl}
          type="text" 
          id="name" 
          name="name" 
          placeholder="name"
          value={name}
          autoComplete="off"
          autoCapitalize="off"
          onChange={ (e) => setName(e.target.value) }>
        </input><br />
        <input
          ref={messageEl}
          disabled={ name.trim().length === 0 }
          type="text" 
          id="message" 
          name="message" 
          placeholder="message"
          value={message}
          autoComplete="off"
          autoCapitalize="off"
          onChange={ (e) => setMessage(e.target.value) }>
        </input><br />
        <button 
          disabled={ name.trim().length === 0 || message.trim().length === 0 } 
          type="submit">
            Send Message
        </button>
      </form>
      <div className='Chat-messages'>
        { chat.map((c) => 
          <div key={Date.now() * Math.random()}>
            <span style={{fontWeight: 'bold'}}>{c.name}: </span>
            {c.message}
          </div>
        )}
      </div>
    </React.Fragment>
  );
}

export default Chat;
